  },
  mounted() {
    if (this.$store.state.user.token === null) {
      this.$router.push("/");
      event_bus.$emit("toast", { message: "Unauthorized", type: "error" });
    } else {
      const element = document.getElementById("google-map-register");
      const options = {
        zoom: 15,
        center: new google.maps.LatLng(39.6027245, -8.4039402)
      };
      this.map = new google.maps.Map(element, options);
      google.maps.event.addListener(this.map, "click", event =>
        this.setMarker(event.latLng)
      );
    }
  },
  methods: {
    register() {
      if (
        this.id !== "" &&
        this.password !== "" &&
        this.address !== "" &&
        this.district !== "" &&
        this.locality !== "" &&
        this.marker !== null
      ) {
        event_bus.$emit("waiting", true);
        this.id =
          this.id.substring(0, 8) +
          "-" +
          this.id.substring(8, 12) +
          "-" +
          this.id.substring(12, 16) +
          "-" +
          this.id.substring(16, 20) +
          "-" +
          this.id.substring(20, 32);

        let vitabox = {
          latitude: this.marker.getPosition().lat(),
          longitude: this.marker.getPosition().lng(),
          address: this.address,
          email: this.$store.state.user.email,
          password: this.password,
          sponsor: true,
          active: false,
          id: this.id,
          district: this.district
            .toLowerCase()
            .replace(/[]/g, "e")
            .replace(/[]/g, "u")
            .replace(/[A]/g, "a")
            .replace(/[]/g, "c")
            .replace(/[]/g, "i")
            .replace(/[]/g, "n")
            .replace(/[]/g, "u")
            .replace(/[]/g, "o")
            .replace(/[ ]/g, "_"),
          locality: this.locality
            .toLowerCase()
            .replace(/[]/g, "e")
            .replace(/[]/g, "u")
            .replace(/[A]/g, "a")
            .replace(/[]/g, "c")
            .replace(/[]/g, "i")
            .replace(/[]/g, "n")
            .replace(/[]/g, "u")
            .replace(/[]/g, "o")
            .replace(/[ ]/g, "_")
        };
        event_bus.$data.http
          .post("/vitabox/" + this.id + "/register", vitabox)
          .then(response => {
            vitabox.registered = true;
            this.$store.commit("addVitaboxToList", vitabox);
            event_bus.$emit("toast", {
              message: "vitabox was successfully registered",
              type: "success"
            });
            event_bus.$emit("waiting", false);
          })
          .catch(error => {
            if (error.response) {
              event_bus.$emit("toast", {
                message: error.response.data,
                type: "error"
              });
            } else {
              event_bus.$emit("toast", {
                message: error.message,
                type: "error"
              });
            }
            event_bus.$emit("waiting", false);
          });
      } else {
        event_bus.$emit("toast", {
          message: "insert all the fields and the marker at map",
          type: "error"
        });
      }
    },
    setMarker(location) {
      if (this.marker !== null) {
        this.marker.setMap(null);
      }
      this.marker = new google.maps.Marker({
        position: location,
        map: this.map
      });
    }
  }
};
</script>

<style>
#google-map-register {
  width: 100%;
  height: 300px;
  margin: 0 auto;
  background-color: transparent;
}
.vitabox_register_form {
  background-color: transparent;
}
</style>


